@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/montserrat.woff") format("woff"), url("../../fonts/montserrat.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/montserrat-medium.woff") format("woff"), url("../../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Myriad Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/myriad-pro.woff") format("woff"), url("../../fonts/myriad-pro.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Myriad Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/myriad-pro-bold.woff") format("woff"), url("../../fonts/myriad-pro-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #1F1A17;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #1F1A17;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.arrow {
  stroke: #6E7275;
  fill: rgba(255, 255, 255, 0);
}
.arrow.active {
  stroke: #DA251C;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 16px;
  position: relative;
}
@media (min-width: 768px) {
  .container {
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 100px;
  }
}
.container--header {
  padding-top: 30px;
}
@media (min-width: 1440px) {
  .container--header {
    padding-top: 64px;
  }
}

[data-button=open] {
  border: none;
  background-color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  padding-right: 2px;
}
[data-button=open]:hover, [data-button=open]:focus, [data-button=open]:active {
  border-radius: 4px;
}

[data-button=close] {
  background-color: #ffffff;
  border: none;
  width: 24px;
  height: 24px;
  position: relative;
}
[data-button=close] svg {
  position: absolute;
  top: 0;
  left: 0;
}
.main-header {
  margin-bottom: 20px;
  background-color: #F6F6F6;
  border-radius: 10px;
  padding: 16px;
}
.main-header__logo img {
  width: 48px;
  height: 64px;
}
@media (min-width: 768px) {
  .main-header__logo img {
    width: 330px;
    height: 82px;
  }
}
.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-header__contacts {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.main-header__contacts a {
  font-size: 16px;
  font-weight: 400;
  line-height: 21.6px;
  text-align: left;
}
.main-header h1 {
  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
}
@media (min-width: 768px) {
  .main-header h1 {
    font-size: 36px;
    line-height: 43.2px;
  }
}
@media (min-width: 1440px) {
  .main-header h1 {
    font-size: 40px;
    line-height: 54px;
  }
}
.main-header__list {
  display: none;
}
.main-header__list a {
  font-size: 16px;
  font-weight: 500;
  line-height: 21.6px;
  border-radius: 100px;
  border: 1.5px solid #DA251C;
  display: block;
  padding: 16px;
}
@media (min-width: 768px) {
  .main-header__list {
    display: flex;
    gap: 30px;
  }
}
.navigation__list li a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
}
.navigation__list li a div {
  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
}

.navigation__wrapper {
  background-color: #F6F6F6;
  transition: all 1s;
  border-radius: 10px;
}
.navigation__wrapper button {
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: none;
  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
  padding: 16px;
  width: 100%;
  text-align: left;
  transition: all 1s;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0px solid #E7E7E7;
}
@media (min-width: 768px) {
  .navigation__wrapper button {
    display: none;
  }
}
.navigation__wrapper button svg {
  transform: rotate(90deg);
  transition: all 0.5s ease-in-out;
}
.navigation__wrapper button.active {
  border-bottom: 1px solid #E7E7E7;
  transition: all 0.3s ease-in-out;
}
.navigation__wrapper button.active svg {
  transform: rotate(630deg);
  transition: all 0.5s ease-in-out;
}
.navigation__wrapper li ul {
  display: none;
}

.navigation__list {
  height: 0;
  overflow: hidden;
  transition: height 0.5s ease-in-out;
  padding: 0 16px;
}
@media (min-width: 768px) {
  .navigation__list {
    height: max-content !important;
    padding: 16px;
  }
}
.navigation__list li {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}
.navigation__list li a {
  border-bottom: 1px solid #E7E7E7;
}
@media (min-width: 768px) {
  .navigation__list li {
    opacity: 1 !important;
  }
}
.navigation__list li:hover svg, .navigation__list li:focus svg, .navigation__list li:active svg {
  stroke: #DA251C;
}
.navigation__list li:hover a, .navigation__list li:focus a, .navigation__list li:active a {
  color: #DA251C;
  border-bottom: 1px solid #DA251C;
}
.navigation__list li.active-link a {
  color: #DA251C;
  border-bottom: 1px solid #DA251C;
}
.navigation__list li.active-link svg {
  stroke: #DA251C;
}
.navigation__list.active {
  height: auto;
  padding: 10px 16px;
}
.navigation__list.active li {
  opacity: 1;
}

